@import '../../../_mixins.scss';
  .addpersonnelList {
    width: 30%;
    min-width: 460px;
    height: 600px;
    background: #fff;
    position: fixed;
    z-index: 9999 !important;
    top: 50%;
    left: 50%;
    margin-left: -15%;
    margin-top: -300px;
    border: 1px solid $essentialColour;
    border-radius: 5px;
    .addpersonnelList-allCheck{
      padding-left:50px;
      >.el-checkbox {
        width: 90%;
        display: block;
        color: $essentialColour;
        margin: 20px 10px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
    >h4 {
      background: $essentialColour;
      color: #fff;
      padding: 10px 0;
      text-align: center;
    }
    >span {
      position: absolute;
      right: 10px;
      top: 0;
      width: 20px;
      height: 20px;
      color: #fff;
      font-size: 30px;
    }
    >.personnelList-search {
      width: 80%;
      margin: 20px auto;
      >.personnelListKey {
        >i {
          cursor: pointer;
          color: $essentialColour;
          font-size: 18px;
        }
      }
    }
    >.el-radio-group {
      padding-left: 50px;
      width: 100%;
      height: 60%;
      overflow: auto;
      >.el-radio {
        width: 90%;
        display: block;
        color: $essentialColour;
        margin: 20px 10px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
    >.el-checkbox-group {
      padding-left: 50px;
      width: 100%;
      height: 60%;
      overflow: auto;
      >.el-checkbox {
        width: 90%;
        display: block;
        color: $essentialColour;
        margin: 20px 10px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
    .btn-section {
      display: block;
      margin: 10px auto 0 auto;
      width: 230px;
      height: 34px;
    }
  }
  .fade-enter-active,
  .fade-leave-active {
    transition: opacity .2s
  }
  .fade-enter,
  .fade-leave-to {
    opacity: 0
  }




