@import '../../../_mixins.scss';
.inform-page{
    background: #FFF;
    overflow: hidden;
    .inform-header{
        padding: 20px 40px 20px 22px;
        @include css3(display, flex);
        justify-content: space-between;
        align-items: center;
        >.infom-header-back{
            @include css3(display, flex);
            align-items: center;
            cursor: pointer;
            >span{
                margin-left: 6px;
            }   
        }
        >.add-btn{
            background: $addBtn;
            color: #fff;
            line-height: 36px;
            width: 70px;
            outline: none;
            border: 0;
            padding: 0;
            border-radius: 6px;
            
        }
    }
    .inform-table{
        text-align: center;
        width: 100%;
        border-left: 0;        
        .is-leaf{
            text-align: center;
            >.cell{
                color: $essentialColour;   
            } 
        }
        .el-table__header{
            tr{
                >th:nth-last-of-type(2)>.cell{
                    padding-right: 40px;
                    padding-left: 6px;
                }                
            }
   
        }        
        .el-table__row{
            td:last-of-type{
                >.cell{
                    @include css3(display, flex);
                    justify-content: space-between;
                    padding: 2px 40px 2px 6px;
                    .el-button{
                        border:0;
                        height:25px;
                        width:60px;
                        padding:4px 0;
                        span{
                            color:#fff;
                        }
                    }
                }                
            }

        }
        .inform-table-editor{
            background: $editor;
        }
        .inform-table-delete{
            background: $delete;
        }         
    }
    .inform-table::after{
        width: 0;
    }     
    .inform-pagination{
        float: right;
        padding: 20px 40px;
    }
    .checkInform-shade {
        top: 0;
        left: 0;
        position: fixed;
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,0.6);
        z-index: 88;
        @include css3(display, flex);
        justify-content: center;
        align-items: center;
        .checkInform-window {     
            width: 50%;
            min-width: 600px;
            max-height: 90%;
            min-height: 400px;
            border: 1px solid $essentialColour;
            background: #fff;
            @include css3(display, flex);
            flex-direction: column;
            @include border-radius(6px);
            .add-checkInform-top {
                background: $essentialColour;
                padding: 10px 0;
                text-align: center;
                color: #fff;
                >i {
                    @include float(right);
                    margin-right: 10px;
                }
            }
            .checkInform-content-wrap{
                width: 94%;
                height: 100%;
                margin: 0 auto;
                overflow-y: auto;
                >h4{
                    padding: 10px 0 4px 0;
                    font-weight: bold;
                }
                .checkInform-content{
                    p{
                        padding-bottom: 10px;
                    }
                    p>img{
                        width: 100%;
                    }
                }                
            }
            .inform-option{
                width: 90%;
                height: 100%;
                margin: 0 auto;
                overflow-y: auto;
                >h4{
                    padding: 10px 0 4px 0;
                    font-weight: bold;
                }
                .el-radio-group{
                    display: block;
                }
                .el-radio__label{
                    font-weight: normal;
                }
                .inform-el-option{
                    display: block;
                    margin-left: 0;
                    padding: 3px 0;
                }
                .option-inform-result{
                    float: right;
                    margin-right: 50px;
                    
                }
            }                        
        }
    }    
    .checkInform-enter-active,
    .checkInform-leave-active {
        transition: opacity .4s
    }
    .checkInform-enter,
    .checkInform-leave-to {
        opacity: 0
    }        
}



