@import '../../../_mixins.scss';
.votes-page{
    background: #FFF;
    overflow: hidden;
    >.votes-header{
        padding: 20px 0;
        margin: 0 40px 0 22px;
        @include css3(display, flex);
        justify-content: space-between;
        border-bottom: 3px solid $essentialColour;
        align-items: center;
        >.votes-header-back{
            @include css3(display, flex);
            align-items: center;
            >span{
                margin-left: 6px;
            }
        }
    }
    >.votesList{
        margin: 0 40px 0 22px;
        >.votes-title {
            margin: 20px 0 0;
            color: #999;
        }
        .votesContent{
            display: flex;
            justify-content: space-around;
            padding:40px 0;
            border-bottom: 3px solid $essentialColour;
            >.votesMsg{
                width:50%;
                min-width: 400px;
                margin-right:20px;
                border:1px solid $essentialColour;
                >h3{
                    padding:15px 0;
                    text-align: center;
                    border-bottom: 1px solid $essentialColour;
                }
                >.voteOption{
                    padding:20px;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    border-bottom: 1px solid $essentialColour;
                    font-size: 16px;
                }
                >.voteOption:last-child{
                    border-bottom:0;
                }
            }
            >.myEcharts{
                flex:1;
                min-width: 500px;
                text-align: center;
                >div{
                    width:100%!important;
                    min-width: 500px;
                    >canvas{
                        width:100%!important;
                        min-width: 500px;
                    }
                }
            }
        }
        >.votesContent:last-child{
            border-bottom:0;
        }
    }
}


