@import '../../../_mixins.scss';
.personalInfo-page{
    background: #FFF;
    overflow: hidden;
    .personalInfo-header{
        padding: 20px 40px 20px 22px;
        @include css3(display, flex);
        justify-content: space-between;
        align-items: center;
        >.personalInfo-header-back{
            @include css3(display, flex);
            align-items: center;
            cursor: pointer;
            >span{
                margin-left: 6px;
            }   
        }
        >.add-btn{
            background: $addBtn;
            color: #fff;
            line-height: 36px;
            width: 70px;
            outline: none;
            border: 0;
            padding: 0;
            border-radius: 6px;
            
        }
    }
    .personalInfo-table{
        text-align: center;
        width: 100%;
        border-left: 0;        
        .is-leaf{
            text-align: center;
            >.cell{
                color: $essentialColour;   
            } 
        }
        .el-table__header{
            tr{
                >th:nth-last-of-type(2)>.cell{
                    padding-right: 40px;
                    padding-left: 6px;
                }                
            }
   
        }        
        .el-table__row{
            td:last-of-type{
                >.cell{
                    @include css3(display, flex);
                    justify-content: space-between;
                    padding: 2px 40px 2px 6px;
                    .el-button{
                        border:0;
                        height:25px;
                        width:60px;
                        padding:4px 0;
                        span{
                            color:#fff;
                        }
                    }
                }                
            }
        }
        .personalInfo-table-editor{
            background: $editor;
        }
        .personalInfo-table-delete{
            background: $delete;
        }         
    }
    .personalInfo-table::after{
        width: 0;
    }     
    .personalInfo-pagination{
        float: right;
        padding: 20px 40px;
    }
}



