@import '../../../_mixins.scss';
.cls {
    width: 100%;
    padding: 0 10px 10px;
    .treeNode {
        width: 100%;
        >.node-scope {
            margin: 0 10px;
            >div {
                padding: 10px 0 10px 10px;
                min-width: 250px;
                &:hover {
                    .btnHandle {
                        display: block;
                    }
                    background:$firstList;
                }
                .treeName {
                    font-size: 18px;
                    vertical-align: middle;
                }
                .changeName {
                    font-size: 18px;
                    width: 110px;
                    border: 0;
                    vertical-align: middle;
                    color: #000;
                }
                .btnHandle {
                    display: none;
                    >.btn {
                        margin-right: 8px;
                        margin-top: 4px;
                        padding: 0;
                        width: 20px;
                        height: 20px;
                        z-index: 3;
                    }
                }
                .treeArrows {
                    padding: 5px 15px 5px 10px;
                    color: #999;
                    vertical-align: top;
                }
            }
        }
    }
    .treeActiveStyle {
        background: #f7f7f7;
    }
}

.cls>li>.node-scope>div {
    margin: 0 0 0 -20px;
    color: #999;
    padding-right: 60px;
}

.cls>li>.node-scope>div>p {
    display: inline-block;
    width: 28px;
    height: 28px;
    background: url('../../../assets/images/ic_grade_gray.png') no-repeat;
    vertical-align: middle;
}

.cls>li>.node-scope>ol>li>.node-scope>div {
    position: relative;
    color: $essentialColour !important;
    padding-right: 30px;
    margin: 10px 0;
}

.cls>li>.node-scope>ol>li>.node-scope>ol>li>.node-scope>div {
    padding-right: 40px;
    margin: 10px 0;
}

.cls>li>.node-scope>ol>li>.node-scope>ol>li>.node-scope>ol>li>.node-scope>div {
    padding-right: 50px;
    margin: 10px 0;
}

.cls>li>.node-scope>ol>li>.node-scope>ol>li>.node-scope>ol>li>.node-scope>ol>li>.node-scope>div {
    padding-right: 60px;
    margin: 10px 0;
}

.cls>li>.node-scope>ol>li>div>div::before {
    background: $essentialColour;
    bottom: 20px;
    content: "";
    height: 8px;
    left: -12px;
    margin-top: 15px;
    position: absolute;
    right: auto;
    width: 8px;
    border-radius: 50%;
}

.cls>li>.node-scope>ol>li>div>div::after {
    border-left: 1px solid $essentialColour;
    bottom: 0;
    content: "";
    left: -8px;
    position: absolute;
    top: 0;
    margin: -10px 0;
}

.cls>li>.node-scope>ol>li>div>ol>li {
    position: relative;
    color: $essentialColour !important;
}

.cls>li>.node-scope>ol>li>div>ol>li::after {
    border-left: 1px solid $essentialColour;
    bottom: 0;
    content: "";
    left: -8px;
    position: absolute;
    top: 0;
}

.cls>li>.node-scope>.treeHandle {
    background: $essentialColour;
    color: #f7f7f7;
    &:hover {
        background: $essentialColour !important;
    }
    >div>span>i {
        color: #f7f7f7;
    }
}


