@import '../../../_mixins.scss';
.note {
  width: 100%;
  height:100%;
  background: #fff;
  @include box-shadow(3px, 5px, 5px, rgba(0, 0, 0, .2));
  .note-header {
    padding: 20px 40px 20px 22px !important;
    @include css3(display, flex);
    justify-content: space-between;
    >p {
      @include css3(display, flex);
      align-items: center;
      cursor: pointer;
      span {
        margin-left: 6px;
      }
    }
    >div {
      .el-button {
        height: 40px;
        width: 70px;
        background: $addBtn;
        color: #fff;
        padding:0;
      }
      >.el-button:first-child{
        background:$essentialColour;
      }
    }
  }
  .note-main {
    // @include css3(display, flex);
    padding:0 0 20px 0;
    .note-table {
      >.table {
        margin: 0;
        text-align: center;
        border-bottom: 1px solid #ddd;
        border-top: 1px solid #ddd;
        .trActive {
          background: $essentialColour;
          color: #fff;
          .tdActive::after {
            content: "";
            display: inline-block;
            width: 0;
            height: 0;
            position: absolute;
            right: -3px;
            z-index: 2;
            top: 8px;
            border-top: solid 13px transparent;
            border-right: solid 13px #fff;
            border-bottom: solid 13px transparent;
          }
        }
        >tbody{
          >tr{
            td:nth-child(2){
              word-break:break-all;
              width:230px;
            }
            td:nth-child(3){
              position:relative
            }
          }
        }
        tr {
          th {
            border-bottom: 1px solid #ddd;
            background: #EEF1F6;
            text-align: center;
            vertical-align: top;
            color: $essentialColour;
            .el-checkbox {
              color: $essentialColour;
              .el-checkbox__inner {
                border-color: $essentialColour;
              }
            }
          }
          td {
            .el-checkbox {
              .el-checkbox__inner {
                border-color: $essentialColour;
              }
            }
          }
        }
      }
    }
    .note-info {
      margin: auto;
      position: fixed;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      width:630px;
      min-width: 630px;
      height: 80%;
      border: 1px solid #2dbad2;
      background: #fff;
      z-index: 9999 !important;
      border-radius: 5px;
      overflow: auto;
      .note-colse {
        position: absolute;
        right:0;
        top:0;
        font-size: 24px;
        padding:0 5px;
        color:$essentialColour;;
      }
      .note-message {
        height:100%;
        display: flex;
        flex-direction: column;
        border-top: 1px solid $essentialColour;
        overflow: hidden;
        >.pdf-Handle{
          padding:10px 0;
          display: flex;
          justify-content: space-around;
          justify-content: center;
          text-align: center;
          >.pdf-zoom{
            >span{
              margin:0 5px;
              color:$essentialColour;
            }
          }
          >.pdf-flip-over{
            display: flex;
            justify-content: center;
            align-items: center;
            .pageNumber{
              overflow: hidden;
              display: flex;
              justify-content: space-around;
              align-items: center;
              border:1px solid $essentialColour; 
              @include border-radius(5px);
              padding:5px 10px;
              >span{
                width:25px;
              }
              >input{
                margin-left:5px;
                width:20px;
                border:0;
                outline: none;
              }
            }
            
            >span{
              display: inline-block;
              width:25px;
              height:30px;
              vertical-align: bottom;
              line-height: 30px;
              text-align: center;
              >i{
                color:$essentialColour;
              }
            }
          }
          >.pdf-bookmark{
            position: relative;
            >span{
              color:$essentialColour;
              display: block;
              width:40px;
              height:30px;
              border:1px solid $essentialColour; 
              @include border-radius(3px);
              >i{
                display: inline-block;
                padding-left:3px;
                line-height: 30px;
              }
            }
            .bookMark-list{
              position: absolute;
              top:30px;
              right:0;
              z-index: 888;
              width:200px;
              text-align: left;
              // padding:10px;
              // background:#fff;
              border:1px solid $essentialColour;
              @include border-radius(4px);
              margin-top:-1px;
            }
          }
        }
        >h5{
          margin:10px 0 0 30px;
        }
        >.pdf-file {
          height: 100%;width: 100%;
          overflow-y:auto;
          position: relative;
          word-break:break-all;
          margin-left:20px;
          padding:0 0 40px 0;
          // text-indent: 2em;
          // text-align: center;
          >#myCanvas{
              position: absolute;
              top:0;
              left:0;
              z-index: 3;
              // margin-bottom: 150px;
          }
          >#theCanvas{
            position: absolute;
            top:0;
            left:0;
            // margin-bottom: 150px;
          }
        }
      }
    }
  }
}




