@import '../../../_mixins.scss';
.checkRecord-page{
    background: #FFF;
    overflow: hidden;
    >.checkRecord-header{
        padding: 20px 0;
        margin: 0 40px 0 22px;
        @include css3(display, flex);
        justify-content: space-between;
        border-bottom: 3px solid $essentialColour;
        align-items: center;
        >.checkRecord-header-back{
            @include css3(display, flex);
            align-items: center;
            >span{
                margin-left: 6px;
            }   
        }
        .add-btn{
            background: $addBtn;
            color: #fff;
            line-height: 36px;
            width: 70px;
            outline: none;
            border: 0;
            padding: 0;
            border-radius: 6px;
            
        }
        .add-export{
            margin-right: 12px;
            background: $essentialColour;
        }
    }
    >.checkRecord-body{
        margin: 0 10%;
        padding: 20px 0;
        .fa{
            color: $essentialColour;
        }        
        .checkRecord-table{
            border-collapse: collapse;
            width: 100%;
            tr{
                >td{
                    border: 1px solid $essentialColour;
                    padding: 8px 0;
                }
                >td:nth-of-type(1){
                    width: 20%;
                    text-align: center;
                    color: $essentialColour;
                    background: $firstList;
                }
                >td:nth-of-type(2){
                    padding-left: 10px;
                }
                >.editor-td{
                    height: 200px;                   
                }                                
            }
            .checkRecord-table-add{
                color: $essentialColour;
                font-size: 16px;                
                display: inline-block;
                margin-left: 4px;
            }                                       
        }
    }   
}        


