@import '../../../_mixins.scss';
.addOfficiaConference {
  display: flex;
  flex-direction: column;
  background: #FFF;
  height:100%;
  >.addOfficiaConference-header {
    padding: 20px 0;
    margin: 0 40px 0 22px;
    @include css3(display, flex);
    justify-content: space-between;
    border-bottom: 3px solid $essentialColour;
    align-items: center;
    >.addOfficiaConference-header-back {
      @include css3(display, flex);
      align-items: center;
      >span {
        margin-left: 6px;
      }
    }
    >div {
      >.add-btn {
        background: $addBtn;
        color: #fff;
        line-height: 36px;
        width: 70px;
        outline: none;
        border: 0;
        padding: 0;
        border-radius: 6px;
        margin-left:10px;
      }
    }
  }
  >.allMsg {
    flex:1;
    overflow-x: hidden;
    overflow-y: scroll;
    position: relative;
    left: 16px;
    height: 100%;
    >.addOfficiaConference-body {
      margin: 0 10%;
      padding-top: 30px;
      .fa {
        color: $essentialColour;
      }
      .addOfficiaConference-table {
        border-collapse: collapse;
        width: 100%;
        tr {
          >td {
            border: 1px solid $essentialColour;
            padding: 8px 0;
          }
          >td:nth-of-type(1) {
            width: 20%;
            text-align: center;
            color: $essentialColour;
            background: $firstList;
          }
          >td:nth-of-type(2) {
            padding-left: 10px;
            >.el-tag {
              margin-right: 10px;
              margin-left: 10px;
            }
          }
        }
        tr:nth-child(5) {
          >td:nth-child(2) {
            padding: 0;
            .quill-editor {
              >.ql-container {
                border: 0;
                height: 510px;
                word-wrap: break-word;
              }
              >.ql-toolbar {
                border: 0;
                border-bottom: 1px solid #ccc;
              }
            }
          }
        }
        >.second-line {
          >td:nth-of-type(1) {
            border: 0;
            background: none;
          }
        }
        .fa-plus {
          margin-left: 12px;
        }
      }
    }
    .addOfficiaConference-table-input {
      width: 98%;
      outline: none;
      border: 0;
    }
    .addOfficiaConference-table-input-exception {
      width: 76%;
    }
    .addOfficiaConference-table-add {
      color: $essentialColour;
      font-size: $systemFontSize;
      display: inline-block;
      margin-left: 4px;
    }
    .el-checkbox {
      font-weight: normal;
      margin-bottom: 0;
      .el-checkbox__inner {
        border: 1px solid $essentialColour;
      }
      .is-checked>.el-checkbox__inner {
        background: $essentialColour;
      }
    }
    >.addTopicTable {
      width: 80%;
      margin: 0 auto;
      padding-top: 30px;
      
      // >.header-topic {
      //   margin: 20px 0;
      //   color: #999;
      // }
      >div {
        position: relative;
        >.delete-Topic{
          position: absolute;
          right:0;
          top:0;
          width:25px;
          height: 25px;
        }
        >.table {
          margin-top: 30px;
          >tbody {
            >tr {
              >th {
                border: 1px solid $essentialColour;
                background: $firstList;
                text-align: center;
                vertical-align: middle;
                color: $essentialColour;
                >.ps{
                  font-size: 10px;
                  color:#888;
                }
              }
              >td {
                padding: 15px 10px;
                border: 1px solid $essentialColour;
                vertical-align: middle;
                .el-tag {
                  margin-right: 10px;
                }
                >input {
                  width: 100%;
                  border: 0;
                  outline: none;
                  height: 30px;
                }
                >select {
                  width: 100%;
                  height: 20px;
                  border: 0;
                  background: #fff;
                  outline: none;
                }
                >.quill-editor {
                  >.ql-container {
                    height: 400px;
                    border: 0;
                    word-wrap: break-word;
                  }
                  >.ql-toolbar {
                    border: 0;
                    border-bottom: 1px solid #ccc;
                  }
                }
                .el-checkbox {
                  font-weight: normal;
                  .el-checkbox__inner {
                    border-color: $essentialColour;
                  }
                }
                >.fileupload-button {
                  background: #fff;
                  color: $essentialColour;
                  line-height: 36px;
                  width: 70px;
                  outline: none;
                  border: 0;
                  padding: 0;
                  border-radius: 6px;
                }
                >i {
                  color: $essentialColour;
                }
                .officia-addTopic {
                  color: $essentialColour;
                  margin-left: 5px;
                  cursor: pointer;
                  >i {
                    margin-right: 5px;
                  }
                }
              }
            }
          }
        }
        >.addTopicTable-footer {
          margin: 0 10%;
          padding: 30px 0;
          >.header-vote {
            margin: 20px 0;
            color: #999;
          }
          .fa {
            color: $essentialColour;
          }
          .header-table {
            border-collapse: collapse;
            width: 100%;
            text-align: center;
            margin-bottom: 10px;
            >caption {
              >button {
                height: 40px;
                width: 70px;
                background: $addBtn;
                color: #fff;
                float: right;
              }
            }
            td {
              border: 1px solid $essentialColour;
              padding: 8px 10px;
            }
            .tabActive {
              background: $essentialColour;
            }
          }
          .Vote-Table-List {
            margin-bottom: 15px;
            position: relative;
            >.el-button {
              position: absolute;
              top: 0;
              right: -46px;
              z-index: 200;
              border-color: $essentialColour;
              border-top-left-radius: 0;
              border-bottom-left-radius: 0;
            }
            .vote-table {
              border-collapse: collapse;
              position: relative;
              width: 100%;
              th {
                border: 1px solid $essentialColour;
              }
              td {
                border: 1px solid $essentialColour;
                padding: 8px 10px;
                &:hover {
                  >.fa-times {
                    display: block;
                  }
                }
                .options {
                  width: 95%;
                  border: 0;
                  outline: none;
                }
                >.fa-times {
                  float: right;
                  display: none;
                  padding: 4px;
                }
              }
              .footer-table-td11,
              .footer-table-td12,
              .footer-table-td13,
              .footer-table-td16 {
                width: 20%;
                text-align: center;
                color: $essentialColour;
                background: $firstList;
              }
              >tr>td:nth-of-type(2) {
                padding-left: 10px;
              }
              >tr:last-of-type>td:nth-of-type(2) {
                text-align: center;
              }
              .footer-table-td23,
              .footer-table-td24,
              .footer-table-td25 {
                padding-left: 10px;
                .el-checkbox {
                  font-weight: normal;
                  margin-bottom: 0;
                }
              }
            }
          }
          .add-Vote {
            padding: 8px;
            display: block;
            text-align: center;
            border: 1px solid $essentialColour;
          }
        }
      }
    }
    >.parting-line {
      height: 2px;
      background: $essentialColour;
      margin: 0 40px 0 22px;
    }
  }
}

.addTopicTable-table-input {
  width: 98%;
  outline: none;
  border: 0;
}

.addTopicTable-table-input-exception {
  width: 76%;
}

.addTopicTable-table-add {
  color: $essentialColour;
  display: inline-block;
  margin-left: 4px;
}

.el-checkbox {
  font-weight: normal;
  margin-bottom: 0;
  .el-checkbox__inner {
    border: 1px solid $essentialColour;
  }
  .is-checked>.el-checkbox__inner {
    background: $essentialColour;
  }
}

.file-upload-table {
  position: fixed;
  width: 540px;
  bottom: 0;
  right: 0;
  border: 2px solid #5bc0de;
  z-index: 250;
  td,
  th {
    border-collapse: collapse;
    padding: 20px 10px;
    text-align: center;
  }
  td {
    background: #fff;
  }
  .progress-show {
    width: 170px;
  }
  .file-name {
    width: 190px;
  }
  .progress {
    margin: 0;
  }
  .fa-window-close {
    float: right;
    position: relative;
    left: 6px;
    bottom: 14px;
  }
  .upload-all-wrap {
    padding: 10px 10px;
    text-align: right;
    .upload-all {
      background: #5bc0de;
      color: #fff;
      line-height: 36px;
      width: 70px;
      outline: none;
      border: 0;
      padding: 0;
      border-radius: 6px;
    }
  }
}

.parting-line {
  height: 2px;
  background: $essentialColour;
  margin: 0 40px 0 22px;
}



