@import '../../../_mixins.scss';
.topic{
    width:100%;
    background:#fff;
    @include box-shadow(3px, 5px, 5px, rgba(0, 0, 0, .2));
    .topic-header{
        padding:20px 40px 20px 22px !important; 
        @include css3(display,flex);
        justify-content: space-between;
        >p{
            @include css3(display,flex);
            align-items: center;
            cursor: pointer;
            span{
                margin-left:6px;
            } 
        }
        >div{
            >.el-button{
                height:40px;
                width:70px;
                background:$addBtn;
                color:#fff;
                padding:0;
            }
        }
    }
    >.topic-table{
        text-align: center;
        >.table{
            border-bottom: 1px solid #ddd;
            border-top: 1px solid #ddd;
            margin:0;
            >thead{            
                >tr{    
                    height:40px;
                    background:#EEF1F6;
                    >th{
                        border-bottom:1px solid #ddd;
                        text-align: center;
                        color:$essentialColour;
                    }
                    >th:first-child{
                        text-align: left;
                        padding-left:40px !important;
                    }
                    >th:last-child {
                        padding-right:35px;
                    }
                }
            }
            >tbody{
                >tr{
                    height:40px;
                    border-top:1px solid #ddd;
                    >td{
                        border:0;
                        vertical-align: baseline;
                        >.el-button{
                            border:0;
                            height:25px;
                            width:60px;
                            padding:4px 0;
                            margin: 0;
                            >span{
                                color:#fff;
                            }
                        }
                        >.el-button:nth-child(1){
                            background:$addBtn;
                        }
                        >.el-button:nth-child(2){
                            background:$editor;
                        }
                        >.el-button:nth-child(3){
                            background:$delete;
                        }
                        .forbid{
                            background:#ccc!important;
                        }
                    }
                    >td:first-child {
                        text-align: left;
                        padding-left:50px !important;
                    }
                    >td:last-child {
                        display: inline-block;
                        text-align: center;
                        padding: 8px 36px 8px 8px;
                        width: 240px;
                    }
                }
            }
        }
    }
    >.el-pagination{
        text-align: right;
        padding:20px 40px 20px 0;
    }
}


