@import '../../_mixins.scss';
.login-page{
    height: 100%;
    display: flex;
    flex-direction: column;
    .login-header{
        padding:20px 20px;
        background: $baseColor;
        position: static !important;
        .login-header-left{
            color: #fff;
        }
        .login-header-dropDown{
            color: #fff;
            float: right;     
        }          
    }
    .login-regist-screen{
        flex: 1;
        display: flex;justify-content: center;align-items: center;
        .login-regist-window{
            width: 400px;
            border: 1px solid $essentialColour;
            border-radius: 8px 8px 6px 6px/8px 8px 6px 6px;
            background: #fff;
            >.login-regist-title{
                border-radius: 6px 6px 0 0/6px 6px 0 0;
                padding: 10px 0;
                text-align: center;
                color: #fff;
                font-size: 18px;
                background: $essentialColour;
            }
            >.login-regist-form{
                padding: 0 14%;
                >.login-regist-input-wrap{
                    padding-top: 24px;
                    display: flex;
                    >div:first-of-type{
                        width: 40px;height: 40px;
                    }
                    >div:last-of-type{
                        flex: 1;
                        display: flex;justify-content: center;
                        align-items: center;
                        background: $inputColor;
                        >input{
                            border: 0;
                            outline: none;
                            width: 94%;
                            background: $inputColor;
                        }                            
                    }
                    .el-form-item__error{
                        top: -20px;
                        left: -36px;
                    }    
                }
                .email-hint{
                    padding: 14px 2px 0 2px;
                }
                .new-user-regist{
                    display: flex;justify-content: space-between;
                    padding: 0 2px;
                    >span{
                        color: $essentialColour;
                        font-size: 14px;
                        padding-top: 6px;
                        cursor: pointer;
                    }
                }
                .login-regist-submit-btn{
                    margin: 20px 0;
                    width: 100%;
                    color: #fff;
                    background: $essentialColour;
                    border: 0;
                    border-radius: 4px;
                }
            }
            .email-icon{
                background: $inputLogo url(../../assets/images/ic_login_email.png) center no-repeat;
            }
            .password-icon{
                background: $inputLogo url(../../assets/images/ic_login_password.png) center no-repeat;
            }
            .user-icon{
                background: $inputLogo url(../../assets/images/ic_login_user.png) center no-repeat;
            }
        }
        .login-regist-form .el-input__inner{
            background: $inputColor;
            outline: none;
            border-radius: 0;
            padding: 6px 10px;
            height: 100%;
            border: 1px solid $inputColor;
        }
        .el-input{
            height: 40px;
        }
        .el-form-item__content{
            margin-left: 0 !important;
            width: 100%;
        }
        .el-form-item{
            margin: 0;
        }            
    }
}
.el-message{
    top: 60px !important;
}           


