@import '../../_mixins.scss';
.system {
  height: 100%;
  @include css3(display, flex);
  flex-direction: column;
  position: relative;
  overflow-y: hidden;
  .app-main {
    flex: 1;
    @include css3(display, flex);
    justify-content: space-between;
    height: 100%;
    width: 100%;
    .navBar {
      width: 250px;
      height: 100%;
      overflow: hidden;
      background: $baseColor;
      margin-right:-2px;
        >.nodes{
          position: relative;
          left:16px;
          height:100%;
          overflow-y:auto;
          overflow-x:hidden;
          margin-left: -16px;
        }
      .btn-section {
        display: block;
        margin: 0 auto;
        width: 230px;
        height: 34px;
        margin-left: 10px;
        margin-bottom: 20px;
      }
    }
    .slideOut {
      width: 0;
      transition: width 500ms ease 0s;
    }
    .slideIn {
      transition: width 500ms ease 0s;
      width: 250;
    }
    .exhibition-info {
      flex: 1;
      height: 100%;
      overflow: hidden;
      padding: 0 40px 20px 20px;
      background: #ECF0F5;
      @include css3(display, flex);
      flex-direction: column;
      .main-header-wrap{
        overflow: hidden;
      }      
      .main-header {          
        position: relative;left: 16px;
        background: #fff;
        min-height: 50px;
        padding: 0 56px 0 36px;
        @include box-shadow(1px, 2px, 5px, rgba(0, 0, 0, .2));
        .main-header-left {
          margin-top: 15px;
          @include float();
          >i {
            display: inline-block;
            width: 20px;
            height: 20px;
            background: url('../../assets/images/ic_hm_location.png') no-repeat;
            margin-right: 5px;
          }
          >.appPresent {
            display: inline-block;
            vertical-align: top;
            margin-top: 2px;
            font-size: $systemFontSize;
          }
          .main-crumbs {
            display: inline-block;
            vertical-align: top;
            margin-top: 4px;
            >span {
              font-size:$systemFontSize;
            }
          }
        }
        .main-header-right {
          @include float(right);
          .searchKey {
            margin-top: 10px;
            >i {
              font-size: 18px;
            }
            >input {
              height: 30px;
              font-size: $systemFontSize;
              border: 0;
              background: #ECF0F5;
              border-radius: 6px;
            }
          }
        }
      }
      .main-content-wrap{
          overflow: hidden;
          height: 100%;
          flex: 1;
          margin-top: 30px;
      }
      .main-content {
        overflow-x: hidden;
        overflow-y: scroll;
        position: relative;left: 16px;
        height: 100%;
      }
    }
  }
  .section-shade {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: #000;
    opacity: 0.7;
    z-index: 1000 !important;
  }
  .section-window {
    margin: auto;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 40%;
    min-width: 460px;
    height: 255px;
    border: 1px solid $essentialColour;
    background: #fff;
    z-index: 9999 !important;
    @include border-radius(6px);
    .add-section-top {
      background: $essentialColour;
      padding: 10px;
      color: #fff;
      >i {
        @include float(right);
      }
    }
    .add-section-info {
      padding: 20px 20px 0;
    }
    .add-section-btn {
      padding-left: 20px;
    }
    .save-btn{
        margin-top: 40px;
    }
  }
  .staff-window {
    @extend .section-window;
  }
  .fade-enter-active,
  .fade-leave-active {
    transition: opacity .2s
  }
  .fade-enter,
  .fade-leave-to {
    opacity: 0
  }
  @media (min-width:1400px){
    .navigation{
      display: flex;
      flex-direction: column;
      width: 55%;
      min-width: 1000px;
      min-height: 700px;
      position: fixed;
      z-index: 9999 !important;
      top: 50%;
      left: 50%;
      margin-left: -28%;
      margin-top: -350px;
      border: 1px solid $essentialColour;
      border-radius: 3px !important;
      background: #fff;
      .navigation-top{
          display: flex;
          justify-content: space-around;
          height:620px;
          background: #fff;
          border-bottom: 1px solid $essentialColour;
          >span{
              flex:1;
              padding:0 20px;
          }
          >span:first-child{
            border-right: 1px solid $essentialColour;
              img{
                  display: block;
                  border:0;
                  width:100%;
              }
          }
          >span:last-child{
              img{
                  display: block;
                  border:0;
                  width:100%;
              }
          }
      }
      >.navigation-buttom{
          flex:1;
          text-align: center;
          background: #fff;
          margin:10px 0;
          >p{
              >span{
                  display: inline-block;
                  font-size: 24px;
                  vertical-align: top;
                  margin:10px 10px 0 0;
                  color:#000;
              }
              >i{
                  display: inline-block;
                  width:78px;
                  height:48px;
                  background:url('../../assets/images/ic_arrow.png') no-repeat;
              }
          }
      }
    }
  }
  @media (max-width:1400px){
    .navigation{
      display: flex;
      flex-direction: column;
      width: 70%;
      min-width: 820px;
      min-height:550px;
      position: fixed;
      z-index: 9999 !important;
      top: 50%;
      left: 50%;
      margin-left: -35%;
      margin-top: -275px;
      border: 1px solid $essentialColour;
      border-radius: 3px !important;
      background: #fff;
      .navigation-top{
          display: flex;
          justify-content: space-around;
          height:480px;
          background: #fff;
          border-bottom: 1px solid $essentialColour;
          >span{
              flex:1;
              padding:0 20px;
          }
          >span:first-child{
            border-right: 1px solid $essentialColour;
              img{
                  display: block;
                  border:0;
                  height:470px;
                  width:100%;
              }
          }
          >span:last-child{
              img{
                  display: block;
                  border:0;
                  height:470px;
                  width:100%;
              }
          }
      }
      >.navigation-buttom{
          flex:1;
          text-align: center;
          background: #fff;
          margin:10px 0;
          >p{
              >span{
                  display: inline-block;
                  font-size: 20px;
                  vertical-align: top;
                  margin:10px 10px 0 0;
                  color:#000;
              }
              >i{
                  display: inline-block;
                  width:78px;
                  height:48px;
                  background:url('../../assets/images/ic_arrow.png') no-repeat;
              }
          }
      }
    }
  }
}



