@import '../../../_mixins.scss';
.device-page{
    background: #FFF;
    overflow: hidden;
    .device-header{
        padding: 20px 40px 20px 22px;
        @include css3(display, flex);
        justify-content: space-between;
        align-items: center;
        >.device-header-back{
            @include css3(display, flex);
            align-items: center;
            cursor: pointer;
            >span{
                margin-left: 6px;
            }   
        }
        .add-btn{
            background: $addBtn;
            color: #fff;
            line-height: 36px;
            width: 70px;
            outline: none;
            border: 0;
            padding: 0;
            border-radius: 6px;
            
        }
    }
    .device-table{
        text-align: center;
        width: 100%;
        border-left: 0;
        .is-leaf{
            text-align: center;
            >.cell{
                color: $essentialColour; 
            } 
        }
        .el-table__header{
            tr{
                >th:nth-last-of-type(2)>.cell{
                    padding-right: 40px;
                    padding-left: 6px;
                }                
            }
   
        }        
        .el-table__row{
            td:last-of-type{
                >.cell{
                    @include css3(display, flex);
                    justify-content: space-between;
                    padding: 2px 40px 2px 6px;
                    .el-button{
                        border:0;
                        height:25px;
                        width:60px;
                        padding:4px 0;
                        span{
                            color:#fff;
                        }
                    }
                }                
            }

        }
        .device-table-editor{
            background: $editor;
        }
        .device-table-delete{
            background: $delete;
        }        
    }
    .device-table::after{
        width: 0;
    }
    .device-pagination{
        float: right;
        padding: 20px 40px;
    }
    .device-shade {
        top: 0;
        left: 0;
        position: fixed;
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,0.6);
        z-index: 88;
    }
    .device-window {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        margin: auto;        
        width: 40%;
        min-width: 460px;
        height: 330px;
        border: 1px solid $essentialColour;
        background: #fff;
        @include border-radius(6px);
        .add-device-top {
            background: $essentialColour;
            padding: 10px;
            color: #fff;
            >i {
                @include float(right);
            }
        }
        .add-device-info {
            padding: 10px 20px 0 20px;
            >.device-input-wrap{
                padding-top: 14px;
            }
        }
        .add-device-btn {
            padding-left: 20px;
            .device-create-btn{
                background: $essentialColour;
                color: #fff;
                border: 0;
            }
        }
    }
    .addDevice-enter-active,
    .addDevice-leave-active {
        transition: opacity .4s
    }
    .addDevice-enter,
    .addDevice-leave-to {
        opacity: 0
    }            
}


