@import '../../../_mixins.scss';
.topicList {
  width: 100%;
  height:100%;
  background: #fff;
  @include box-shadow(3px, 5px, 5px, rgba(0, 0, 0, .2));
  .topicList-header {
    padding: 20px 40px 20px 22px !important;
    @include css3(display, flex);
    justify-content: space-between;
    >p {
      @include css3(display, flex);
      align-items: center;
      cursor: pointer;
      span {
        margin-left: 6px;
      }
    }
    >div {
      .el-button {
        height: 40px;
        width: 70px;
        background: $addBtn;
        color: #fff;
        padding:0;
      }
    }
  }
  .topicList-main {
    @include css3(display, flex);
    padding:0 10px 20px 0;
    height:90%;
    .topicList-table {
      min-width:400px;
      >.table {
        margin: 0;
        text-align: center;
        border-bottom: 1px solid #ddd;
        border-top: 1px solid #ddd;
        .trActive {
          background: $essentialColour;
          color: #fff;
          .tdActive::after {
            content: "";
            display: inline-block;
            width: 0;
            height: 0;
            position: absolute;
            right: -3px;
            z-index: 2;
            top: 8px;
            border-top: solid 13px transparent;
            border-right: solid 13px #fff;
            border-bottom: solid 13px transparent;
          }
        }
        >tbody{
          >tr{
            td:nth-child(2){
              word-break:break-all;
              width:230px;
            }
            td:nth-child(3){
              position:relative
            }
          }
        }
        tr {
          th {
            border-bottom: 1px solid #ddd;
            background: #EEF1F6;
            text-align: center;
            vertical-align: top;
            color: $essentialColour;
            .el-checkbox {
              color: $essentialColour;
              .el-checkbox__inner {
                border-color: $essentialColour;
              }
            }
          }
          td {
            .el-checkbox {
              .el-checkbox__inner {
                border-color: $essentialColour;
              }
            }
          }
        }
      }
      >.addTopic-link {
        padding: 10px 20px;
        color: $essentialColour;
        display: inline-block;
        >i {
          margin-right: 5px;
        }
      }
    }
    .topicList-info {
      flex:1;
      height:100%;
      position: relative;
      border: 1px solid $essentialColour;
      margin-left: -1px;
      .topicList-content {
        @include css3(display, flex);
        text-align: center;
        p {
          height: 40px;
          border-right: 1px solid $essentialColour;
          line-height: 40px;
        }
        p:nth-child(1) {
          width: 25%;
          min-width:110px;
        }
        p:nth-child(2) {
          width: 40%;
          min-width:110px;
          text-overflow:ellipsis;overflow:hidden;white-space:nowrap;
        }
        p:nth-child(3) {
          flex: 1;
          min-width:153px;
          margin-right: -1px;
          >.el-button {
            border: 0;
            height: 25px;
            width: 60px;
            padding: 4px 0;
            >span {
              color: #fff;
            }
          }
          // >.el-button:nth-child(1){
          //   background:#60CEB8;
          // }
          >.el-button:nth-child(1){
            background:$editor;
          }
          >.el-button:nth-child(2){
            background:$delete;
          }
        }
      }
      .topicList-message {
        height:90%;
        overflow-y:auto;
        border-top: 1px solid $essentialColour;
        padding: 50px 20px 10px 20px;
        >h3{
          padding:20px 0;
          border-bottom:1px solid $essentialColour;
        }
        >p {
          word-break:break-all;
          margin: 30px 0 50px 0;
          text-indent: 2em;
          >p{
            width:100%;
            height:100%;
          }
        }
        >a {
          position: absolute;
          color: $essentialColour;
          bottom: 14px;
          right: 30px;
        }
        >.topic-VoteList{
          line-height: 30px;
          margin: 20px 0;
          >label{
            display: block;
            >input{
              margin-right:10px;
              vertical-align: middle;
              margin-bottom: 5px;
            }
          }
        }
        >.topic-Attachments{
          margin:20px 0;
        }
      }
    }
  }
}



