@import '../../../_mixins.scss';
.conference-page{
    background: #FFF;
    overflow: hidden;
    .conference-header{
        padding: 20px 40px 20px 22px;
        @include css3(display, flex);
        justify-content: space-between;
        align-items: center;
        >.conference-header-back{
            @include css3(display, flex);
            align-items: center;
            cursor: pointer;
            >span{
                margin-left: 6px;
            }   
        }
        >.add-btn{
            background: $addBtn;
            color: #fff;
            line-height: 36px;
            width: 70px;
            outline: none;
            border: 0;
            padding: 0;
            border-radius: 6px;
            
        }
    }
    >.conference-table{
        text-align: center;
        >.table{
            border-bottom: 1px solid #ddd;
            border-top: 1px solid #ddd;
            margin:0;
            >thead{            
                >tr{    
                    height:40px;
                    background:#EEF1F6;
                    >th{
                        border-bottom:1px solid #ddd;
                        text-align: center;
                        color:$essentialColour;
                    }
                    >th:first-child{
                        text-align: left;
                        padding-left:40px !important;
                    }
                    >th:last-child {
                        padding-right:35px;
                    }
                }
            }
            >tbody{
                >tr{
                    height:40px;
                    border-top:1px solid #ddd;
                    >td{
                        border:0;
                        vertical-align: baseline;
                        >.el-button{
                            border:0;
                            height:25px;
                            width:60px;
                            padding:4px 0;
                            margin: 0;
                            >span{
                                color:#fff;
                            }
                        }
                        >.el-button:nth-child(1){
                            background:$essentialColour;
                            
                        }
                        >.el-button:nth-child(2){
                            background:$delete;
                            
                        }
                        >.el-button:nth-child(3){
                            background:$addBtn;
                        }
                        .forbid{
                            background:#ccc!important;
                        }
                    }
                    >td:first-child {
                        text-align: left;
                        padding-left:50px !important;
                    }
                    >td:last-child {
                        display: inline-block;
                        text-align: center;
                        padding: 8px 36px 8px 8px;
                        width: 240px;
                    }
                }
            }
        }
    } 
    .conference-pagination{
        float: right;
        padding: 20px 40px;
    }    
}


