@import '../../../_mixins.scss';
.home {
  .home-menu {
    flex-wrap: wrap;
    @media (min-width:1176px){
        .col-md-4{
            width: 33.333%;
            float: left;
        }
    }
    @media (max-width:1176px) and (min-width:886px){
        .col-sm-6{
            width: 50%;
            float: left;
        }
    }
    @media (max-width:886px){
        .col-xs-12{
            width: 100%;
            float: left;
        }
    }    
    .home-topic {
      >div{
        padding: 100px 0;
        background: #8BC34A;
        @include box-shadow(3px, 5px, 5px, rgba(0, 0, 0, .2));
        text-align: center;
        cursor: pointer;
        margin-bottom: 50px;
        >span {
          display: inline-block;
          width:76px;
          height:76px;
          background:url('../../../assets/images/ic_book.png') no-repeat;
          margin-right: 30px;
          vertical-align: top;
        }
        .home-title {
          height:76px;
          display: inline-block;
          border-left: 2px solid #fff;
          color: #fff;
          padding:10px 0 10px 30px;
          >h2{
            font-family: "方正兰亭中黑";
          }
          >p{
            font-family:"sans-serif","方正兰亭黑";
            font-size: 15px;
            text-align: left;
            font-weight: bold;
          }
        }
      }
      
    }
    .home-conference{
        @extend .home-topic;
        >div{
          background:#00BCD4;
        }
        
    }
    .home-vote{
        @extend .home-topic;
        >div{
          padding: 80px 0;
          background:#BA68C8;
        }
        
    }
    .home-message{
        @extend .home-vote;
        >div{
          padding: 80px 0;
          >span{
            background:url('../../../assets/images/ic_notice.png') no-repeat;
          }
        }
    }
    .home-crew{
        @extend .home-topic;
        >div{
          padding: 80px 0;
          background:#FFA726;
          >span{
            background:url('../../../assets/images/ic_person.png') no-repeat;
          }
        }
        
    }
    .home-facility{
        @extend .home-topic;
        >div{
          padding: 80px 0;
          >span{
            background:url('../../../assets/images/ic_boox.png') no-repeat;
          }
        }
    }
  }
}



