@import '../../../_mixins.scss';
.compileOfficial {
  display: flex;
  flex-direction: column;
  width: 100%;
  height:100%;
  background: #fff;
  @include box-shadow(3px, 5px, 5px, rgba(0, 0, 0, .2));
  padding-bottom: 20px;
  .compileOfficial-header {
    padding: 20px 0;
    margin: 0 40px 0 22px;
    border-bottom: 2px solid $essentialColour;
    @include css3(display, flex);
    justify-content: space-between;
    >p {
      @include css3(display, flex);
      align-items: center;
      cursor: pointer;
      span {
        margin-left: 6px;
      }
    }
    >.compileOfficial-handle {
      .el-button {
        height: 40px;
        width: 70px;
        background: $addBtn;
        color: #fff;
        padding:0;
      }
    }
  }
  >.compileOfficial-allMsg{
    flex:1;
    overflow-x: hidden;
    overflow-y: scroll;
    position: relative;
    left: 16px;
    height: 100%;
    >.compileOfficialTable {
      padding: 20px 0;
      margin: 0 10%;
      .el-checkbox {
        font-weight: normal;
        margin-bottom: 0;
        .el-checkbox__inner {
          border: 1px solid $essentialColour;
        }
        .is-checked>.el-checkbox__inner {
          background: $essentialColour;
        }
      }
      .compileOfficialTable-table {
        border-collapse: collapse;
        width: 100%;
        tr {
          >td {
            border: 1px solid $essentialColour;
            padding: 10px 0;
            >input {
              width: 98%;
              outline: none;
              border: 0;
            }
            >.addConference-table-input {
              width: 98%;
              outline: none;
              border: 0;
            }
          }
          >td:nth-of-type(1) {
            width: 20%;
            text-align: center;
            color: $essentialColour;
            background: $firstList;
          }
          >td:nth-of-type(2) {
            padding: 10px;
            >.el-tag {
              margin-right: 10px;
            }
            >.compileOfficialTable-table-add {
              color: $essentialColour;
              display: inline-block;
              margin-left: 4px;
            }
          }
        }
        // tr:nth-child(4) {
        //   td:nth-of-type(2) {
        //       padding: 0;
        //       >.quill-editor{
        //           >.ql-container{
        //               border:0;
        //               height:510px;
        //               word-wrap:break-word;
        //           }
        //           >.ql-toolbar{
        //               border:0;
        //               border-bottom: 1px solid #ccc;
        //           }
        //       }
        //   } 
        // }
      //   .compileOfficialTable-table-theme {
      //     width: 98%;
      //     outline: none;
      //     border: 0;
      //   }
      //   .compileOfficialTable-table-content {
      //     @extend .compileOfficialTable-table-theme;
      //     height: 80px;
      //     resize: none;
      //   }
        .fa-plus {
          margin-left: 12px;
          color: $essentialColour;
        }
        .compileOfficialTable-table-add {
          color: $essentialColour;
          display: inline-block;
          margin-left: 4px;
        }
      }
    }
    >.compileOfficial-Topic {
      margin: 0 10% 30px;
      border: 1px solid $essentialColour;
      >.compileOfficial-content {
        @include css3(display, flex);
        text-align: center;
        justify-content: space-around;
        p {
          border-right: 1px solid $essentialColour;
          line-height: 40px;
          flex: 1;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
              >.el-button {
                  border:0;
                  height:25px;
                  width:60px;
                  padding:4px 0;
                  margin: 0;
                  >span{
                      color:#fff;
                  }
              }
              .el-button:first-child{
                background:$editor;
              }
              .el-button:last-child{
                background:$delete;
              }
              
        }
        p:last-child {
          margin-right: -1px;
          // width:10%;
          min-width:130px;
        }
      }
      >.compileOfficial-message {
        height: 90%;
        overflow-y: auto;
        border-top: 1px solid $essentialColour;
        padding: 30px 20px 10px 20px;
        >h3 {
          text-align: center;
          margin-bottom: 20px;
        }
        >p {
          word-break: break-all;
          margin: 30px 0 50px 0;
          text-indent: 2em;
        }
        >a {
          position: absolute;
          color: $essentialColour;
          bottom: 14px;
          right: 30px;
        }
        >h4{
          font-weight: bold;
          padding:20px 0;
          border-bottom:1px solid $essentialColour;
        }
        >.compileOfficial-Vote {
          line-height: 30px;
          margin: 20px 0;
          >h4{
            margin:10px 0;
          }
          >label {
            display: block;
            >input {
              margin-right: 10px;
              vertical-align: middle;
              margin-bottom: 5px;
            }
          }
        }
        >.compileOfficial-Attachments{
            margin:20px 0;
        }
      }
    }
  }
  
}



