@import './_mixins.scss';
.app{
    height: 100%;
    overflow-y:hidden;
}
.el-message-box{
    >.el-message-box__btns{
        >button:first-child{
            float:right;
            margin-left:10px !important;
            background:$essentialColour !important;
            color:#fff;
            border:1px solid $essentialColour;
        }
        >button:last-child{
            background:#fff !important;
            color:#1f2d3d;
            border:1px solid #ccc;
        }
    }
}


