@import '../../../_mixins.scss';
.sendInform{
    width:100%;
    background:#fff;
    @include box-shadow(3px, 5px, 5px, rgba(0, 0, 0, .2));
    .sendInform-header{
        padding:20px 0;
        margin:0 40px 0 22px;
        border-bottom:2px solid $essentialColour;
        @include css3(display,flex);
        justify-content: space-between;
        >p{
            @include css3(display,flex);
            align-items: center;
            cursor: pointer;
            span{
                margin-left:6px;
            } 
        }
        >div{
            .el-button{
                height:40px;
                width:70px;
                background:$addBtn;
                color:#fff;
            }
        }
    }
    >.sendInformTable{
        padding: 20px 0;
        margin: 0 10%;
        .quill-editor{
            >.ql-container{
                border:0;
                height:510px;
                word-wrap:break-word;
            }
            >.ql-toolbar{
                border:0;
                border-bottom: 1px solid #ccc;
                >.ql-formats{
                    margin: 0;
                }
            }
        }
        .el-checkbox{
            font-weight: normal;
            margin-bottom: 0;
            .el-checkbox__inner{
                border: 1px solid $essentialColour;
            }
            .is-checked>.el-checkbox__inner{
                background: $essentialColour;
            }
        }        
        .sendInformTable-table{
            border-collapse: collapse;
            width: 100%;
            tr{
                >td{
                    padding:0;
                    word-break:break-all;
                    border: 1px solid $essentialColour;
                    padding: 10px 0;
                }
                >td:nth-of-type(1){
                    width: 20%;
                    text-align: center;
                    color: $essentialColour;
                    background: $firstList;
                }
                >td:nth-of-type(2){
                    padding-left: 10px;
                }                
            }
            tr:last-child{
                td:last-child{
                    padding:0;
                }
            }
            .sendInformTable-table-theme{
                width: 98%;
                outline: none;border: 0;
            }
            .sendInformTable-table-content{
                @extend .sendInformTable-table-theme;
                height: 80px;
                resize: none;
            }
            .fa-plus{
                margin-left: 12px;
                color: $essentialColour;
            }            
            .sendInformTable-table-add{
                color: $essentialColour;              
                display: inline-block;
                margin-left: 4px;
            }                        
        }        
    }
}


