@import '../../../_mixins.scss';
.app-header{
    padding:10px 20px;
    background: $baseColor;
    position: static !important;
    .app-header-left{
        >i{
            display: inline-block;
            color:#fff;
            font-size: 20px;
            margin-right:10px;
        }
        >span{
            color:#fff;
            font-size: 20px;
            vertical-align: baseline;
        }
    }
    .app-header-right{
        text-align: right;
        padding-right:30px;
        >span{
            cursor: pointer;
            display: inline-block;
            width:35px;
            height:35px;
            background:#fff;
            @include border-radius(50%);
            margin-top:2px;
        }
        .admin-menu{
            cursor: pointer;
            vertical-align: top;
            margin:10px 0 0 20px;
            >span{
                color:#fff;
            }
        }
        .app-header-dropDown{
            color: #fff;  
            vertical-align: top; 
            margin:12px 12px 0 20px;       
        }
        .exit-system{
            color: #fff;
            display: inline-block;
            margin: 10px 0 0 10px;
            vertical-align: top;
        }
    }
}


