@import '../../../_mixins.scss';
.addRecord-page{
    background: #FFF;
    overflow: hidden;
    >.addRecord-header{
        padding: 20px 0;
        margin: 0 40px 0 22px;
        @include css3(display, flex);
        justify-content: space-between;
        border-bottom: 3px solid $essentialColour;
        align-items: center;
        >.addRecord-header-back{
            @include css3(display, flex);
            align-items: center;
            >span{
                margin-left: 6px;
            }   
        }
        >.addRecord-Handle{
            >.add-btn{
                background: $addBtn;
                color: #fff;
                line-height: 36px;
                width: 70px;
                outline: none;
                border: 0;
                padding: 0;
                border-radius: 6px;
            }
            >.over-btn{
                @extend .add-btn;
            }
        } 
    }
    >.addRecord-body{
        margin: 0 10%;
        padding: 20px 0;
        .fa{
            color: $essentialColour;
        }        
        .addRecord-table{
            border-collapse: collapse;
            width: 100%;
            tr{
                >td{
                    border: 1px solid $essentialColour;
                    padding: 8px 0;
                }
                >td:nth-of-type(1){
                    width: 20%;
                    text-align: center;
                    color: $essentialColour;
                    background: $firstList;
                }
                >td:nth-of-type(2){
                    padding-left: 10px;
                    .el-tag{
                        margin-right:10px;
                    }
                }
                .editor-td {
                    padding:0 !important;
                }
                .quill-editor{
                    >.ql-container{
                        border:0;
                        height:400px;
                        word-wrap:break-word;
                        >.ql-editor{
                            >p{
                                >img{
                                    width:100%;
                                }
                            }
                        }
                    }
                    >.ql-toolbar{
                        border:0;
                        border-bottom: 1px solid #ccc;
                    }
                }                               
            }            
            .fa-plus{
                margin-left: 12px;
            }
            .addRecord-table-input{
                width: 98%;
                outline: none;border: 0;
            }
            .addRecord-table-add{
                color: $essentialColour;
                font-size: $systemFontSize;                
                display: inline-block;
                margin-left: 4px;
            }
            .el-checkbox{
                font-weight: normal;
                margin-bottom: 0;
                .el-checkbox__inner{
                    border: 1px solid $essentialColour;
                }
                .is-checked>.el-checkbox__inner{
                    background: $essentialColour;
                }
            }                        
                           
        }

    }   
}        


