@import '../../../_mixins.scss';
.checkMeeting{
    width:100%;
    background:#fff;
    @include box-shadow(3px, 5px, 5px, rgba(0, 0, 0, .2));
    .checkMeeting-header{
        padding:20px 0;
        margin:0 40px 0 22px;
        border-bottom:2px solid $essentialColour;
        @include css3(display,flex);
        justify-content: space-between;
        >p{
            @include css3(display,flex);
            align-items: center;
            cursor: pointer;
            span{
                margin-left:6px;
            } 
        }
        >div{
            .el-button{
                height:40px;
                width:70px;
                background:$addBtn;
                color:#fff;
            }
        }
    }
    >.checkMeetingTable{
        padding: 20px 0;
        margin: 0 10%;
        .el-checkbox{
            font-weight: normal;
            margin-bottom: 0;
            .el-checkbox__inner{
                border: 1px solid $essentialColour;
            }
            .is-checked>.el-checkbox__inner{
                background: $essentialColour;
            }
        }        
        .checkMeetingTable-table{
            border-collapse: collapse;
            width: 100%;
            tr{
                >td{
                    border: 1px solid $essentialColour;
                    padding: 10px 0;
                }
                >td:nth-of-type(1){
                    width: 20%;
                    text-align: center;
                    color: $essentialColour;
                    background: $firstList;
                }
                >td:nth-of-type(2){
                    padding:10px;                  
                }                
            }                       
        }        
    }
}


