@import '../../../_mixins.scss';
.official {
  width: 100%;
  background: #fff;
  @include box-shadow(3px, 5px, 5px, rgba(0, 0, 0, .2));
  padding-bottom: 20px;
  .official-header {
    padding: 20px 0;
    margin: 0 40px 0 22px;
    border-bottom: 2px solid $essentialColour;
    @include css3(display, flex);
    justify-content: space-between;
    >p {
      @include css3(display, flex);
      align-items: center;
      cursor: pointer;
      span {
        margin-left: 6px;
      }
    }
    >div {
      .el-button {
        height: 40px;
        width: 70px;
        background: $addBtn;
        color: #fff;
      }
    }
  }
  >.officialTable {
    padding: 20px 0;
    margin: 0 10%;
    .el-checkbox {
      font-weight: normal;
      margin-bottom: 0;
      .el-checkbox__inner {
        border: 1px solid $essentialColour;
      }
      .is-checked>.el-checkbox__inner {
        background: $essentialColour;
      }
    }
    .officialTable-table {
      border-collapse: collapse;
      width: 100%;
      tr {
        >td {
          border: 1px solid $essentialColour;
          padding: 10px 0;
        }
        >td:nth-of-type(1) {
          width: 20%;
          text-align: center;
          color: $essentialColour;
          background: $firstList;
        }
        >td:nth-of-type(2) {
          padding: 10px;
          >.el-tag{
            margin-right:10px;
          }
          >.officialTable-table-add{
                color: $essentialColour;              
                display: inline-block;
                margin-left: 4px;
            } 
        }
      }
      .officialTable-table-theme {
        width: 98%;
        outline: none;
        border: 0;
      }
      .officialTable-table-content {
        @extend .officialTable-table-theme;
        height: 80px;
        resize: none;
      }
      .fa-plus {
        margin-left: 12px;
        color: $essentialColour;
      }
      .officialTable-table-add {
        color: $essentialColour;
        display: inline-block;
        margin-left: 4px;
      }
    }
  }
  >.official-Topic {
    margin: 0 10% 30px;
    border: 1px solid $essentialColour;
    >.official-content {
      @include css3(display, flex);
      text-align: center;
      p {
        border-right: 1px solid $essentialColour;
        line-height: 40px;
        flex:1;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap
      }
      p:last-child{
          margin-right:-1px;
          min-width:238px;
      }
    }
    >.official-message {
      height: 90%;
      overflow-y: auto;
      border-top: 1px solid $essentialColour;
      padding: 30px 20px 10px 20px;
      >h3{
          text-align: center;
          margin-bottom: 20px;
      }
      >h4{
        font-weight: bold;
        padding:20px 0;
        border-bottom:1px solid $essentialColour;
      }
      >p {
        word-break: break-all;
        margin: 30px 0 50px 0;
        text-indent: 2em;
      }
      >a {
        position: absolute;
        color: $essentialColour;
        bottom: 14px;
        right: 30px;
      }
      >.official-Vote {
        line-height: 30px;
        margin: 20px 0;
        >h4{
          margin:10px 0;
        }
        >label {
          display: block;
          >input {
            margin-right: 10px;
            vertical-align: middle;
            margin-bottom: 5px;
          }
        }
      }
      >.official-Attachments{
          margin:20px 0;
      }
    }
  }
}



