@import '../../../_mixins.scss';
$addBtn : #60CEB8;
$tableFirstLine : #00BCD4;
$editor : #23a9db;
$delete : #ed6965;
$popup : #2dbad2;
.addLinkman-page{
    background: #FFF;
    overflow: hidden;
    .addLinkman-header{
        padding: 20px 40px 20px 22px;
        @include css3(display, flex);
        justify-content: space-between;
        align-items: center;
        >.addLinkman-header-back{
            @include css3(display, flex);
            align-items: center;
            cursor: pointer;
            >span{
                margin-left: 6px;
            }   
        }
        .add-btn{
            background: $addBtn;
            color: #fff;
            line-height: 36px;
            width: 70px;
            outline: none;
            border: 0;
            padding: 0;
            border-radius: 6px;
        }
    }
    >.addLinkman-table{
        text-align: center;
        >.table{
            border-bottom: 1px solid #ddd;
            border-top: 1px solid #ddd;
            margin:0;
            >thead{            
                >tr{    
                    height:40px;
                    background:#EEF1F6;
                    >th{
                        border-bottom:1px solid #ddd;
                        text-align: center;
                        color:$essentialColour;
                    }
                    >th:first-child{
                        padding-left:40px;
                    }
                    >th:last-child {
                        text-align: right;
                        padding-right: 90px;
                    }
                }
            }
            >tbody{
                >tr{
                    height:40px;
                    border-top:1px solid #ddd;
                    >td{
                        border:0;
                        vertical-align: baseline;
                        >.el-button{
                            border:0;
                            height:25px;
                            width:60px;
                            padding:4px 0;
                            margin: 0;
                            >span{
                                color:#fff;
                            }
                        }
                        >.el-button:nth-child(1){
                            background:$essentialColour;
                            
                        }
                        >.el-button:nth-child(2){
                            background:$delete;
                            
                        }
                        >.el-button:nth-child(3){
                            background:$addBtn;
                        }
                        .forbid{
                            background:#ccc!important;
                        }
                    }
                    >td:first-child {
                        text-align: left;
                        padding-left:50px !important;
                    }
                    >td:last-child {
                        display: inline-block;
                        text-align: right;
                        padding: 8px 40px 8px 8px;
                        min-width: 240px;
                    }
                }
            }
        }
    } 
    .addLinkman-pagination{
        float: right;
        padding: 20px 40px;
    }
    .addLinkman-shade {
        top: 0;
        left: 0;
        position: fixed;
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,0.6);
        z-index: 88;
    }
    .addLinkman-window {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        margin: auto;        
        width: 40%;
        min-width: 460px;
        height: 740px;
        border: 1px solid $popup;
        background: #fff;
        @include border-radius(6px);
        .add-addLinkman-top {
            background: $popup;
            padding: 10px;
            color: #fff;
            >i {
                @include float(right);
            }
        }
        .add-addLinkman-info {
            padding: 10px 20px 0 20px;
            >.el-form-item{
                >.el-form-item__content{
                    .el-select{
                        display: block;
                    }
                }
            }
        }
        .add-addLinkman-btn {
            text-align: center;
            .addLinkman-create-btn{
                background: $popup;
                color: #fff;
                border: 0;
            }
        }
    }
    .addLinkman-editor-window{
        height: 560px;
    }
    .show-enter-active,
    .show-leave-active {
        transition: opacity .4s
    }
    .show-enter,
    .show-leave-to {
        opacity: 0
    }            
}


